.contact {
  position: relative;
  background-image: linear-gradient(
    to top,
    #FFF5F6 82%,
    #ffffff 18%
  );
  padding-top: 43px;
  padding-bottom: 57px;
  @media screen and (min-width: 370px){
    padding-left: 40px;
    padding-right: 40px;
  }
  @media screen and (min-width: 768px) {
    padding-bottom: 79px;
    background-image: linear-gradient(
    to top,
    #FFF5F6 84%,
    #ffffff 16%
  );
  }
  @media screen and (min-width: 1280px) {
    background-image:url(../images/contacts/contacts-bg.png),
    linear-gradient(
    to top,
    #FFF5F6 84%,
    #ffffff 16%);
    background-repeat: no-repeat;
    background-position: 100% 40%;
    background-size: contain;
  }
}


.contact-list{ 
  margin-bottom: 55px;
  @media screen and (min-width: 768px){
    display: flex;
    justify-content: space-between;
    margin-bottom: 81px;
  }
  &__item {
    margin: 20px;
    padding: 43px 20px;
    background: $main-bg-color;
    box-shadow: 0px 8px 30px rgba(212, 20, 67, 0.1);
    border-radius: 24px;
    &:not(:last-child) {
      margin-bottom: 20px;
      @media screen and (min-width: 768px){
        margin-bottom: unset;
        margin-right: 20px;
      }
    }
    @media screen and (min-width: 768px){
      padding: 50px 32px;
      width: 100%;
      margin:unset;
      margin-left: unset;
      margin-right: unset;
    }
  }
  &__title {
    display: inline-block;
    padding-left: 25px;
    padding-right: 25px;
    margin-bottom: 25px;
    border-radius: 4px;
    font-weight: 500;
    font-style: normal;
    font-size: 12px;
    line-height: 1.83;
    text-transform: uppercase;
    color: $main-bg-color;
    background-color: #F0D1A5;
    @media screen and (min-width: 768px){
      font-size: 16px;
      line-height: 1.88;
      margin-bottom: 30px;
    }
  }
  &__title.colored{
      background-color: #C2E297;
    }
  &__city {
    margin-bottom: 10px;
    font-style: normal;
    font-weight: 500;
    font-size: 12px;
    line-height: 1.83;
    @media screen and (min-width: 768px){
      font-size: 14px;
      line-height: 1.86;
      margin-bottom: 14px;
      
    }
  }
  &__text {
    margin-bottom: 25px;
    font-style: normal;
    font-weight: 500;
    font-size: 12px;
    color: $second-text-color;
    @media screen and (min-width: 768px){
      font-size: 14px;
      line-height: 1.64;
    }
      &::after {
        display: block;
        content: "";
        width: 100%;
        height: 1px;
        border: 1px solid #E1E1E1;
        margin-bottom: 25px;
        margin-top: 25px;
        @media screen and (min-width: 768px){
          margin-bottom: 30px;
          margin-top: 30px;
        }
      }
  }

  &__working-days {
    font-style: normal;
    font-weight: 500;
    font-size: 12px;
    line-height: 1.83;
    @media screen and (min-width: 768px){
      font-size: 14px;
      line-height: 1.86;
    }
  }
  &__working-time {
    font-weight: 500;
    font-size: 12px;
    line-height: 1.83;
    color: $section-text-color;
    @media screen and (min-width: 768px){
      font-size: 14px;
      line-height: 1.64;
    }
  }

  //   
  &__working-time:not(:last-child) {
    margin-bottom: 10px;
    @media screen and (min-width: 768px){
      margin-bottom: 15px;
    }
  }
  &__link {
    display: block;
    font-style: normal;
    font-weight: 500;
    font-size: 12px;
    color: $second-text-color;
    @media screen and (min-width: 768px){
      font-size: 14px;
      line-height: 1.64;
    }
      
  }
}

.contact-box::before {
  display: block;
  content: "";
  width: 100%;
  height: 1px;
  border: 1px solid #E1E1E1;
  margin-bottom: 25px;
  margin-top: 25px;
  @media screen and (min-width: 768px){
    margin-bottom: 30px;
    margin-top: 30px;
  }
}

.btn-container {
  @media screen and (min-width: 768px) {
    display: flex;
    justify-content: center;
  }
}

.contact-btn {
  margin-left: auto;
  margin-right: auto;
  display: block;
  font-weight: 700;
  font-size: 16px;
  line-height: 1.31;
  @media screen and (min-width: 768px) {
    margin: 0;
  }
  
  &.btn__accent {
    color: $main-bg-color;
    margin-bottom: 10px;
    padding-left: 17px;
    padding-right: 17px;
    @media screen and (min-width: 768px) {
      margin-bottom: unset;
      margin-right: 15px;
      padding-left: 30px;
      padding-right: 30px;
    }
  }
  &.btn__light {
    color: $accent;
    padding-left: 32px;
    padding-right: 32px;
    @media screen and (min-width: 768px) {
      padding-left: 38px;
      padding-right: 38px;
    }
    
  }
}

.btn-img {
  margin-left: 8px;
  text-align: center;
}

// -----------MODAL-----------


.modal {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: #ffb8ca;
  border-radius: 5px;
  box-shadow: 0px 8px 30px rgba(212, 20, 67, 0.1);
  min-width: 300px;
  height: 540px;
  @media screen and (min-width: 468px){
    width: 400px;
    height: 540px;
  }
  @media screen and (min-width: 768px){
    width: 528px;
  }
}

.backdrope {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(236, 199, 199, 0.4);
}

.is-hidden {
  opacity: 0;
  pointer-events: none;
  visibility: hidden;
}

.close-btn{
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: 12px;
  right: 12px;
  width: 30px;
  height: 30px;
  border: 1px solid #ffffff;
  border-radius: 50%;
  background-color: transparent;
  cursor: pointer;
  &:hover{
    border-color: $accent;
  }
  &:hover .close-icon{
    border-color: $accent;
    fill: $accent;
  }
}

.close-icon {
  fill: #ffffff;
}

.modal-window {
  padding: 40px 25px;
  @media screen and (min-width: 768px){
    padding: 40px;
  }
  &__title {
    margin-top: 0;
    margin-bottom: 12px;
    font-weight: bold;
    font-size: 20px;
    line-height: 1.15;
    text-align: center;
    color: $accent  ;
  }
}

.form-group {
  display: flex;
  flex-direction: column;
  margin-bottom: 10px;

  &__label {
    margin-bottom: 4px;

    font-weight: normal;
    font-size: 12px;
    line-height: 1.17;
    letter-spacing: 0.04em;
    color: #ffffff;
  }
}

.form-input {
  position: relative;

  &__text {
    margin: 0;
    padding: 0;
    padding-left: 42px;
    height: 40px;
    width: 100%;
    outline: none;
    border: 1px solid rgba(33, 33, 33, 0.2);
    border-radius: 5px;
    
    transition: border-color 250ms $effect;
  }

  &__icon {
    position: absolute;
    top: 50%;
    left: 12px;
    transform: translateY(-50%);
    fill: #ffb8ca;

    transition: fill 250ms $effect;
  }
  &__text:focus + &__icon,
  &__text:hover + &__icon,
  &__text:focus,
  &__text:hover {
    border-color: $accent;
    fill: $accent;
  }
}

.form-group__textarea::placeholder {

  font-weight: normal;
  font-size: 12px;
  line-height: 1.17;
  letter-spacing: 0.04em;
  color: rgba(117, 117, 117, 0.5);
}

.form-group__textarea {
  outline: none;
  border: 1px solid rgba(33, 33, 33, 0.2);
  border-radius: 5px;
  resize: none;
  padding: 12px 16px;
  height: 120px;

  transition: border-color 250ms $effect;
  
  &:focus,
  &:hover {
    border-color: $accent;
  }
}

.modal-btn {
  display: flex;
  align-items: center;
  text-align: center;
  margin-left: auto;
  margin-right: auto;
  margin-top: 30px;

  transition-property: color, background-color;
  transition-duration: 250ms;
  transition-timing-function:$effect;

  &:hover {
    background-color: $accent;
    color: $main-bg-color;
  }
}



