.advantages {
    background-color: #FFF5F6;
    padding-bottom: 97px;
    @media screen and (min-width: 768px) {
        padding-bottom: 136px;
    }
    @media screen and (min-width: 1280px) {
        padding-bottom: 139px;
    }
}

.advantages-list {
    @media screen and (min-width: 768px) {
        display: flex;
    }
}

.advantages-item {
    width: 100%;
    @media screen and (min-width: 768px) {
        flex-basis: calc((100% - 120px) / 3);
        margin-left: 60px;
    }

     @media screen and (min-width: 1280px) {
        flex-basis: calc((100% - 60px) / 3);
        margin-left: 30px;
    }

    &__icon {
        margin-bottom: 16px;
        @media screen and (min-width: 768px) {
            margin-bottom: 36px;
        }
    
         @media screen and (min-width: 1280px) {
            margin-bottom: 40px;
        }
    }

    &__title {
        font-family: $second-font;
        font-weight: 400;
        font-size: 36px;
        line-height: 1.15;
        letter-spacing: 0.04em;
        text-transform: uppercase;
        text-align: left;
        color: $accent;
        margin-bottom: 16px;
        @media screen and (min-width: 768px) {
            margin-bottom: 13px;
            font-size: 48px;
            line-height: 1.46;
        }
        @media screen and (min-width: 1280px) {
            margin-bottom: 15px;
            font-size: 58px;
            line-height: 1.14;
        }
    }

    &__text {
        font-family: $main-font;
        font-weight: 700;
        font-size: 12px;
        line-height: 1.67;
        letter-spacing: 0.04em;
        color: $main-text-color;
        margin-bottom: 38px;
        @media screen and (min-width: 768px) {
            font-size: 14px;
            line-height: 1.71;
        }
        @media screen and (min-width: 1280px) {
            font-size: 16px;
            line-height: 1.75;
            letter-spacing: 0.02em;
        }
    }

}

